function capitalize(text) {
  let wordsArray = text.toLowerCase().split(' ')
  let capsArray = []

  wordsArray.forEach(word => {
      capsArray.push(word[0].toUpperCase() + word.slice(1))
  });

  return capsArray.join(' ')
}

import { createRouter, createWebHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    component: () => import('../views/Home.vue'),
    meta: {
      title: 'Geoxor | Home',
    },
  },
  {
    path: '/unlock',
    component: () => import('../views/Unlock.vue'),
    meta: {
      title: 'Geoxor | Unlock',
    },
  },
  {
    path: '/faq',
    component: () => import('../views/FAQ.vue'),
    meta: {
      title: 'Geoxor | FAQ',
    },
  },
  {
    path: '/biography',
    component: () => import('../views/Biography.vue'),
    meta: {
      title: 'Geoxor | Biography',
    },
  },
  {
    path: '/contact',
    component: () => import('../views/Contact.vue'),
    meta: {
      title: 'Geoxor | Contact',
    },
  },
  {
    path: '/downloads',
    component: () => import('../views/Downloads.vue'),
    meta: {
      title: 'Geoxor | Downloads',
    },
  },
  {
    path: '/release/:name',
    component: () => import('../views/Release.vue'),
    meta: {
      title: 'Geoxor | Release',
    },
  },
  {
    path: '/behind-the-scenes/',
    component: () => import('../views/Behind the Scenes.vue'),
    meta: {
      title: 'Geoxor | 🍣 Behind the Scenes',
    },
  },
  {
    path: '/early-access/',
    component: () => import('../views/Early Access.vue'),
    meta: {
      title: 'Geoxor | 🍣 Early Access Blog',
    },
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

// this sets the tab title to the current page dynamically
router.beforeEach((to, from, next) => {
  if (to.meta.title) document.title = to.meta.title;
  if (to.path.startsWith('/release')) document.title = "Geoxor - " + capitalize(to.params.name.replace("-", " ")); 
  window.scrollTo(0,0);
  next();
});

export default router
